/** @jsx React.DOM */
"use strict";

var React = require('react/addons');
var moment = require('moment-range');
var RangePicker = require('../dist/range-picker');

var Header = require('./components/header.jsx');
var Footer = require('./components/footer.jsx');
var GithubRibbon = require('./components/github-ribbon.jsx');
var CodeSnippet = require('./components/code-snippet.jsx');
var Install = require('./components/install.jsx');
var Features = require('./components/features.jsx');

function processCodeSnippet(src) {
  var lines = src.split('\n');
  lines.splice(0, 1);
  lines.splice(lines.length - 1, 1);
  return lines.join('\n');
}

var DatePickerRange = React.createClass({displayName: 'DatePickerRange',
  getInitialState: function() {
    return {
      start: this.props.start,
      end: this.props.end,
    };
  },
  handleSelect: function(range) {
    this.setState({
      start: range.start,
      end: range.end
    });
  },
  render: function() {
    var range;

    if (this.state.start && this.state.end) {
      range = moment().range(this.state.start, this.state.end);
    }

    return (
      React.DOM.div(null, 
        this.transferPropsTo(
          RangePicker({
            onSelect: this.handleSelect,
            value: range
          })
        ), 
        React.DOM.div(null, 
          React.DOM.input({type: "text", 
            value: this.state.start ?  this.state.start.format('LL') : null, 
            readOnly: true, 
            placeholder: "Start date"}), 
          React.DOM.input({type: "text", 
            value: this.state.end ? this.state.end.format('LL') : null, 
            readOnly: true, 
            placeholder: "End date"})
        )
      )
    );
  }
});

var DatePickerSingle = React.createClass({displayName: 'DatePickerSingle',
  getInitialState: function() {
    return {
      value: null,
    };
  },
  handleSelect: function(value) {
    this.setState({
      value: value,
    });
  },
  render: function() {
    return React.DOM.div({},
      this.transferPropsTo(
        RangePicker({
          onSelect: this.handleSelect,
          value: this.state.value
        })
      ),
      React.DOM.div(null,
        React.DOM.input({
          type: 'text',
          value: this.state.value ? this.state.value.format('LL') : null,
          readOnly: true
        }, null)
      )
    );
  }
});

var mainCodeSnippet = require('./code-snippets/main.js').toString();

var Homepage = React.createClass({displayName: 'Homepage',
  getDefaultProps: function() {
    return {};
  },

  render: function() {
    var dateRanges = [
      {
        range: moment().range(
          moment().startOf('day'),
          moment().add(2, 'weeks')
        ),
        state: 'available',
        selectable: true
      },
      {
        range: moment().range(
          moment().add(2, 'weeks'),
          moment().add(3, 'weeks')
        ),
        state: 'enquire',
        selectable: true
      },
      {
        range: moment().range(
          moment().add(3, 'weeks'),
          moment().add(3, 'weeks').add(5, 'days')
        ),
        state: 'unavailable',
        selectable: false
      },
      {
        range: moment().range(
          moment().add(3, 'weeks').add(5, 'days'),
          moment().add(10, 'weeks')
        ),
        state: 'available',
        selectable: true
      },
    ];

    var initialStart = moment().add(1, 'weeks').startOf('day');
    var initialEnd = moment().add(2, 'weeks').startOf('day');

    return (
      React.DOM.html(null, 
        React.DOM.head(null, 
          React.DOM.title(null, "React Daterange Picker Demo"), 
          React.DOM.link({href: "//cdnjs.cloudflare.com/ajax/libs/highlight.js/8.0/styles/docco.min.css", rel: "stylesheet", type: "text/css"}), 
          React.DOM.link({href: "css/react-calendar.css", rel: "stylesheet"}), 
          React.DOM.link({href: "css/example.css", rel: "stylesheet"})
        ), 
        React.DOM.body(null, 
          Header(null), 
          GithubRibbon(null), 

          React.DOM.div({className: "content"}, 
            React.DOM.div({className: "example"}, 
              DatePickerRange({
                numberOfCalendars: 2, 
                selectionType: "range", 
                earliestDate: new Date(), 
                dateStates: dateRanges, 
                start: initialStart, 
                end: initialEnd}), 
              CodeSnippet({language: "javascript"}, 
                processCodeSnippet(mainCodeSnippet)
              )
            ), 

            Features(null), 
            Install(null), 

            React.DOM.div({className: "examples"}, 
              React.DOM.h2(null, "Examples"), 

              React.DOM.div({className: "example"}, 
                React.DOM.h4(null, "Range with no date states"), 
                DatePickerRange({
                  numberOfCalendars: 2, 
                  selectionType: "range", 
                  earliestDate: new Date()})
              ), 

              React.DOM.div({className: "example"}, 
                React.DOM.h4(null, "Single with no date states"), 
                DatePickerSingle({
                  numberOfCalendars: 2, 
                  selectionType: "single", 
                  earliestDate: new Date()})
              )
            )
          ), 

          Footer(null), 

          React.DOM.script({type: "text/javascript", src: "//cdnjs.cloudflare.com/ajax/libs/highlight.js/8.0/highlight.min.js", charSet: "utf-8"}), 
          React.DOM.script({type: "text/javascript", src: "//cdnjs.cloudflare.com/ajax/libs/highlight.js/8.0/languages/javascript.min.js", charSet: "utf-8"}), 
          React.DOM.script({src: "build/index.js"})
        )
      )
    );
  }
});

module.exports = Homepage;
