/** @jsx React.DOM */
"use strict";

var React = require('react/addons');
var CodeSnippet = require('./code-snippet');

var Install = React.createClass({displayName: 'Install',
  render: function() {
    return (
      React.DOM.div({className: "install"}, 
        React.DOM.h2(null, "Install"), 
        CodeSnippet({language: "bash", toggle: false}, 
          "npm install react-daterange-picker"
        )
      )
    );
  }
});

module.exports = Install;
