/** @jsx React.DOM */
"use strict";

var React = require('react/addons');

var Footer = React.createClass({displayName: 'Footer',
  render: function() {
    return (
      React.DOM.footer({className: "footer"}, 
        OFSCredit(null), 
        React.DOM.div({className: "footer__links"}, 
          React.DOM.a({href: "http://tech.onefinestay.com/", className: "footer__link"}, "Blog"), 
          React.DOM.a({href: "https://github.com/onefinestay", className: "footer__link"}, "Github"), 
          React.DOM.a({href: "https://twitter.com/buildingOFS", className: "footer__link"}, "Twitter")
        )
      )
    );
  }
});

var OFSCredit = React.createClass({displayName: 'OFSCredit',
  render: function() {
    return (
      React.DOM.div({className: "ofs-credit"}, 
        React.DOM.p({className: "ofs-credit__text"}, "Brought to you by"), 
        React.DOM.a({href: "http://www.onefinestay.com/", className: "ofs-credit__logo"}, 
          React.DOM.img({src: "img/logo.png"})
        )
      )
    );
  }
});

module.exports = Footer;
